import * as React from 'react';
import { BlogCard } from './BlogCard';
import Link from '~src/i18n/components/Link';
import useIntlHelper from '~src/i18n/helpers/useIntlHelper';
//import { graphql, useStaticQuery } from 'gatsby';
import { Index_blog_nodes, Index_blog } from '~src/pages/__generated__/Index';
import styles from './Blog.module.scss';

export const BlogFeed: React.FC<{ blog: Index_blog; isHome?: boolean }> = ({ blog, isHome }) => {
	//const data = useStaticQuery(QUERY);
	const { formatMessage } = useIntlHelper();
	return (
		<div className="inner">
			<div className={styles.posts}>
				<div className={styles.postFeed}>
					{(blog.nodes as Index_blog_nodes[]).map((post: Index_blog_nodes, i: number) => {
						return <BlogCard post={post} i={i} key={i} isHome={isHome} />;
					})}
				</div>
			</div>
			{isHome ? (
				<Link to="/blog">
					<h3 className={styles.readMore}> {isHome ? formatMessage('general.readMore') : null}</h3>
				</Link>
			) : null}
		</div>
	);
};
