import * as React from 'react';
import { graphql } from 'gatsby';
import Layout from '~src/components/Layout/Layout';
import { Index as IndexData } from '~src/pages/__generated__/Index';
import { Navigation } from '~src/components/Layout/Navigation/Navigation';
import HeaderBackground from '~src/components/Layout/Header/HeaderBackground';
import Link from '~src/i18n/components/Link';
//import { PostFeed } from '~src/components/Sections/PostFeed/PostFeed';
import useIntlHelper from '~src/i18n/helpers/useIntlHelper';
import siteLogo from '~src/assets/images/soheil-logo.png';
import indexCover from '~src/assets/images/header-cover.jpg';
import { ArtworksFeed } from '~src/components/Sections/Artworks/Artworks';
import styles from './index.module.scss';
import { BlogFeed } from '~src/components/Sections/Blog/Blog';
import { FilmsFeed } from '~src/components/Sections/Filmography/FilmsFeed';
import { TutorialsFeed } from '~src/components/Sections/Tutorials/Tutorials';

//import { ArtworksFeed } from '../Sections/Artworks/Artworks';
const IndexHero = () => {
	const { formatMessage } = useIntlHelper();
	const site = {
		title: 'meta.title',
		description: 'meta.desc',
		logo: siteLogo,
	};
	return (
		<HeaderBackground
			//fluidImg={data.indexCover.childImageSharp.fluid}
			//fixedImg={data.indexCover.childImageSharp.fixed}
			srcImg={indexCover}>
			<div className="inner">
				<Navigation className="site-nav" isHome={true} />
				<div className="site-header-content">
					<h1 className="site-title">
						{site.logo ? (
							<Link to="/">
								<img className={styles.siteLogo} src={site.logo} alt={formatMessage(site.title)} />
								{/* 			<div className={styles.siteLogoSVG}>
											<Logo />
									</div> */}
							</Link>
						) : (
							<Link to="/">{formatMessage(site.title)}</Link>
						)}
					</h1>
					<h2 className="site-description">
						{formatMessage(site.title)}
						{` | `}
						{formatMessage(site.description)}
					</h2>
				</div>
			</div>
		</HeaderBackground>
	);
};
const IndexPage: React.FC<{ data: IndexData }> = ({ data }) => {
	const stickyRef: React.RefObject<HTMLDivElement> = React.useRef(null);
	const { formatMessage } = useIntlHelper();
	return (
		<Layout template="home" stickyRef={stickyRef} header={<IndexHero />}>
			<div className="inner" ref={stickyRef}>
				<h1 style={{ paddingTop: '20px' }}> {formatMessage('navigation.artworks')}</h1>
				<ArtworksFeed artworks={data.artworks} isHome={true} />
				<h1> {formatMessage('navigation.films')}</h1>
				<FilmsFeed films={data.films} isHome={true} />
				<h1> {formatMessage('navigation.blog')}</h1>
				<BlogFeed blog={data.blog} isHome={true} />
				{/*		<h1> {formatMessage('navigation.tutorials')}</h1>
			 		<TutorialsFeed tutorials={data.tutorials} /> */}
				{/* 	<PostFeed data={data} isHome /> */}
			</div>
		</Layout>
	);
};

export default IndexPage;
export const indexQuery = graphql`
	query Index($locale: String!) {
		artworks: allMdx(
			sort: { fields: [frontmatter___date], order: DESC }
			limit: 6
			filter: { fields: { locale: { eq: $locale } }, frontmatter: { category: { eq: "artworks" } } }
		) {
			nodes {
				fields {
					slug
				}
				frontmatter {
					date(formatString: "DD MMMM YYYY")
					title

					category
					featured
					tags
					image {
						childImageSharp {
							fluid(maxWidth: 700) {
								...GatsbyImageSharpFluid_withWebp_tracedSVG
							}
						}
					}
				}
				excerpt(truncate: true, pruneLength: 200)
				timeToRead
			}
		}
		films: allMdx(
			sort: { fields: [frontmatter___date], order: DESC }
			limit: 6
			filter: { fields: { locale: { eq: $locale } }, frontmatter: { category: { eq: "filmography" } } }
		) {
			nodes {
				fields {
					slug
				}
				frontmatter {
					date(formatString: "DD MMMM YYYY")
					title
					category
					featured
					tags
					image {
						childImageSharp {
							fluid(maxWidth: 700) {
								...GatsbyImageSharpFluid_withWebp_tracedSVG
							}
						}
					}
				}
				excerpt(truncate: true, pruneLength: 200)
				timeToRead
			}
		}
		blog: allMdx(
			sort: { fields: [frontmatter___date], order: DESC }
			limit: 4
			filter: { fields: { locale: { eq: $locale } }, frontmatter: { category: { eq: "blog" } } }
		) {
			nodes {
				fields {
					slug
				}
				frontmatter {
					date(formatString: "DD MMMM YYYY")
					title
					desc
					category
					featured
					tags
					image {
						childImageSharp {
							fluid(maxWidth: 700) {
								...GatsbyImageSharpFluid_withWebp_tracedSVG
							}
						}
					}
				}
				excerpt(truncate: true, pruneLength: 200)
				timeToRead
			}
		}
		tutorials: allMdx(
			sort: { fields: [frontmatter___date], order: DESC }
			limit: 4
			filter: { fields: { locale: { eq: $locale } }, frontmatter: { category: { eq: "tutorials" } } }
		) {
			nodes {
				fields {
					slug
				}
				frontmatter {
					date(formatString: "DD MMMM YYYY")
					title
					category
					featured
					tags
					image {
						childImageSharp {
							fluid(maxWidth: 700) {
								...GatsbyImageSharpFluid_withWebp_tracedSVG
							}
						}
					}
				}
				excerpt(truncate: true, pruneLength: 200)
				timeToRead
			}
		}
	}
`;
