import * as React from 'react';
import styles from './FilmsFeed.module.scss';
//import { graphql, useStaticQuery } from 'gatsby';
import { Index_films, Index_artworks_nodes } from '~src/pages/__generated__/Index';
import Link from '~src/i18n/components/Link';
import useIntlHelper from '~src/i18n/helpers/useIntlHelper';
import { FilmsCard } from './FilmsCard';
export const FilmsFeed: React.FC<{ films: Index_films; isHome?: boolean }> = ({ films, isHome }) => {
	//const data = useStaticQuery(QUERY);
	const { formatMessage } = useIntlHelper();
	return (
		<div className="inner">
			<div className={styles.posts}>
				<div className={styles.postFeed}>
					{(films.nodes as Index_artworks_nodes[]).map((post: Index_artworks_nodes, i: number) => {
						return <FilmsCard post={post} i={i} key={i} isHome={isHome} />;
					})}
				</div>
			</div>
			{isHome ? (
				<Link to="/filmography">
					<h3 className={styles.readMore}> {isHome ? formatMessage('general.readMore') : null}</h3>
				</Link>
			) : null}
		</div>
	);
};
