import * as React from 'react';
import styles from './Artworks.module.scss';
//import { graphql, useStaticQuery } from 'gatsby';
import { Index_artworks, Index_artworks_nodes } from '~src/pages/__generated__/Index';
import useIntlHelper from '~src/i18n/helpers/useIntlHelper';
import Link from '~src/i18n/components/Link';
import { ArtworkCard } from './ArtworkCard';
export const ArtworksFeed: React.FC<{ artworks: Index_artworks; isHome?: boolean }> = ({ artworks, isHome }) => {
	//const data = useStaticQuery(QUERY);
	const { formatMessage } = useIntlHelper();
	return (
		<div className="inner">
			<div className={styles.posts}>
				<div className={styles.postFeed}>
					{(artworks.nodes as Index_artworks_nodes[]).map((post: Index_artworks_nodes, i: number) => {
						return <ArtworkCard post={post} i={i} key={i} isHome={isHome} />;
					})}
				</div>
			</div>
			{isHome ? (
				<Link to="/artworks">
					<h3 className={styles.readMore}> {isHome ? formatMessage('general.readMore') : null}</h3>
				</Link>
			) : null}
		</div>
	);
};
